export { default as Layout } from "../routes/$layout.js";
export { default as ErrorComponent } from "./components/error.js";

export const components = [
	() => import("../routes/index.js"),
	() => import("../routes/kontakt.js"),
	() => import("../routes/blog/index.js"),
	() => import("../routes/blog/[slug].js"),
	() => import("../routes/boty/index.js")
];

export const routes = (d => [
	{
		// index.svelte
		pattern: /^\/$/,
		parts: [
			{ i: 0 }
		]
	},

	{
		// kontakt.svelte
		pattern: /^\/kontakt\/?$/,
		parts: [
			{ i: 1 }
		]
	},

	{
		// blog/index.svelte
		pattern: /^\/blog\/?$/,
		parts: [
			{ i: 2 }
		]
	},

	{
		// blog/[slug].svelte
		pattern: /^\/blog\/([^/]+?)\/?$/,
		parts: [
			null,
			{ i: 3, params: match => ({ slug: d(match[1]) }) }
		]
	},

	{
		// boty/index.svelte
		pattern: /^\/boty\/?$/,
		parts: [
			{ i: 4 }
		]
	}
])(decodeURIComponent);